import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { withStyles } from '@material-ui/core/styles';
import TableCell from '@material-ui/core/TableCell';
import Paper from '@material-ui/core/Paper';
import { observer } from 'mobx-react';
import { ExigeStoreContext } from '../../store/ExigeStore';
import { AutoSizer, Column, Table } from 'react-virtualized';
import OperationDetailsDialog from './OperationDetailsDialog';
import Button from '@material-ui/core/Button';
import VisibilityIcon from '@material-ui/icons/Visibility';
import InfoIcon from '@material-ui/icons/Info';

const styles = (theme) => ({
    flexContainer: {
        display: 'flex',
        alignItems: 'center',
        boxSizing: 'border-box',
    },
    table: {
        '& .ReactVirtualized__Table__headerRow': {
            flip: false,
            paddingRight: theme.direction === 'rtl' ? '0 !important' : undefined,
        },
    },
    tableRow: {
        cursor: 'pointer',
    },
    tableCell: {
        flex: 1,
    },
    noClick: {
        cursor: 'initial',
    },
});

class MuiVirtualizedTable extends React.PureComponent {
    static defaultProps = {
        headerHeight: 48,
        rowHeight: 48,
    };

    getRowClassName = ({ index }) => {
        const { classes, onRowClick } = this.props;

        return clsx(classes.tableRow, classes.flexContainer, {
            [classes.tableRowHover]: index !== -1 && onRowClick != null,
        });
    };

    cellRenderer = ({ cellData, columnIndex, rowData }) => {
        const {
            columns,
            classes,
            rowHeight,
            onRowClick,
            ViewDetailsButton,
            ViewOnMapButton,
        } = this.props;
        if (columnIndex === 3) {
            return (
                <TableCell
                    component="div"
                    className={clsx(classes.tableCell, classes.flexContainer, {
                        [classes.noClick]: onRowClick == null,
                    })}
                    variant="body"
                    style={{ height: rowHeight }}
                    align={
                        (columnIndex != null && columns[columnIndex].numeric) || false
                            ? 'right'
                            : 'left'
                    }
                >
                    <ViewOnMapButton operation={rowData} />
                    <ViewDetailsButton operation={rowData} />
                </TableCell>
            );
        } else {
            return (
                <TableCell
                    component="div"
                    className={clsx(classes.tableCell, classes.flexContainer, {
                        [classes.noClick]: onRowClick == null,
                    })}
                    variant="body"
                    style={{ height: rowHeight }}
                    align={
                        (columnIndex != null && columns[columnIndex].numeric) || false
                            ? 'right'
                            : 'left'
                    }
                >
                    {cellData}
                </TableCell>
            );
        }
    };

    headerRenderer = ({ label, columnIndex }) => {
        const { headerHeight, columns, classes } = this.props;

        return (
            <TableCell
                component="div"
                className={clsx(classes.tableCell, classes.flexContainer, classes.noClick)}
                variant="head"
                style={{ height: headerHeight, backgroundColor: '#212121' }}
                align={columns[columnIndex].numeric || false ? 'right' : 'left'}
            >
                <span>{label}</span>
            </TableCell>
        );
    };

    render() {
        const { classes, columns, rowHeight, headerHeight, ...tableProps } = this.props;
        return (
            <AutoSizer>
                {({ height, width }) => (
                    <Table
                        height={height}
                        width={width}
                        rowHeight={rowHeight}
                        gridStyle={{
                            direction: 'inherit',
                        }}
                        headerHeight={headerHeight}
                        className={classes.table}
                        {...tableProps}
                        rowClassName={this.getRowClassName}
                    >
                        {columns.map(({ dataKey, ...other }, index) => {
                            return (
                                <Column
                                    key={dataKey}
                                    headerRenderer={(headerProps) =>
                                        this.headerRenderer({
                                            ...headerProps,
                                            columnIndex: index,
                                        })
                                    }
                                    className={classes.flexContainer}
                                    cellRenderer={this.cellRenderer}
                                    dataKey={dataKey}
                                    {...other}
                                />
                            );
                        })}
                    </Table>
                )}
            </AutoSizer>
        );
    }
}

MuiVirtualizedTable.propTypes = {
    classes: PropTypes.object.isRequired,
    columns: PropTypes.arrayOf(
        PropTypes.shape({
            dataKey: PropTypes.string.isRequired,
            label: PropTypes.string.isRequired,
            numeric: PropTypes.bool,
            width: PropTypes.number.isRequired,
        })
    ).isRequired,
    headerHeight: PropTypes.number,
    onRowClick: PropTypes.func,
    rowHeight: PropTypes.number,
};

const VirtualizedTable = withStyles(styles)(MuiVirtualizedTable);

const OperationsTable = observer(({ handleDialogClose }) => {
    const exigeStore = useContext(ExigeStoreContext);

    let operations = exigeStore.operations;
    let rows = Object.values(operations);

    const [detailsDialogOpen, setDetailsDialogOpen] = React.useState(false);
    const [detailsDialogData, setDetailsDialogData] = React.useState(false);

    const handleDetailsDialogOpen = (detailsData) => {
        setDetailsDialogOpen(true);
        setDetailsDialogData(detailsData);
    };

    const handleDetailsDialogClose = () => {
        setDetailsDialogOpen(false);
    };

    const ViewOnMapButton = ({ operation }) => {
        return (
            <>
                <Button
                    variant="contained"
                    size="small"
                    onClick={() => {
                        exigeStore.map.cameraCenter.latitude =
                            operation?.volumes[0].volume?.outline_polygon?.vertices[0].lat;
                        exigeStore.map.cameraCenter.longitude =
                            operation?.volumes[0].volume?.outline_polygon?.vertices[0].lng;
                        handleDialogClose();
                    }}
                    startIcon={<VisibilityIcon />}
                    style={{ marginRight: '10px' }}
                >
                    Map
                </Button>
            </>
        );
    };

    const ViewDetailsButton = ({ operation }) => {
        return (
            <>
                <Button
                    variant="contained"
                    size="small"
                    startIcon={<InfoIcon />}
                    onClick={() => {
                        handleDetailsDialogOpen(operation);
                    }}
                >
                    Details
                </Button>
            </>
        );
    };

    return (
        <Paper style={{ height: 500, width: '100%', backgroundColor: '#263238' }}>
            <VirtualizedTable
                rowCount={rows.length}
                rowGetter={({ index }) => rows[index]}
                columns={[
                    {
                        width: 350,
                        label: 'GUFI',
                        dataKey: 'gufi',
                    },
                    {
                        width: 180,
                        label: 'State',
                        dataKey: 'state',
                    },
                    {
                        width: 120,
                        label: 'Owner',
                        dataKey: 'owner',
                    },
                    {
                        width: 220,
                        label: 'Actions',
                        dataKey: 'actions',
                    },
                ]}
                ViewDetailsButton={ViewDetailsButton}
                ViewOnMapButton={ViewOnMapButton}
            />

            <OperationDetailsDialog
                open={detailsDialogOpen}
                handleClose={handleDetailsDialogClose}
                detailsData={detailsDialogData}
            />
        </Paper>
    );
});

export default OperationsTable;
